import React from 'react'
import { Link } from 'react-router-dom'

import '../../stylesheets/homePage.scss'

class Home extends React.Component {

  state = {
    prevPath: ''
  }

  componentDidMount() {
    if (this.props.location.state) {
        this.setState({ prevPath: this.props.location.state.prevPath })
    }
  }

  firstTimeOnPage = (prevPath) => {
    return ['/about', '/experience', '/projects'].includes(prevPath)
  }

  render() {
    const { prevPath } = this.state
    const pathStatus = this.firstTimeOnPage(prevPath)
    return (
      <>
      <section className={`home-main-container ${pathStatus ? 'animated faster zoomIn' : ''}`}>
        <div className={`home-headings ${!pathStatus ? 'animated fadeInUp' : ''}`}>
          <h1 className="home-main-heading">Bheki Maenetja</h1>
          <h2 className="home-sub-heading">Builder. Creative. Problem-Solver.</h2>
          <br />
          <hr />
        </div>
        <div className="home-app-icons">
          <div className={`home-icon-container ${!pathStatus ? 'animated faster zoomIn delay-1s' : ''}`}>
            <Link to="/about">
              <div className="home-app-icon" id="about"></div>
            </Link>
            <br />
            <p className="home-app-name">About Me</p>
          </div>
          <div className={`home-icon-container ${!pathStatus ? 'animated faster zoomIn delay-2s' : ''}`}>
            <Link to="/experience">
              <div className="home-app-icon" id="experience"></div>
            </Link>
            <br />
            <p className="home-app-name">Experience</p>
          </div>
          <div className={`home-icon-container ${!pathStatus ? 'animated faster zoomIn delay-3s' : ''}`}>
            <Link to="/projects">
              <div className="home-app-icon" id="projects"></div>
            </Link>
            <br />
            <p className="home-app-name">Projects</p>
          </div>
        </div>
        <footer className={`home-contact-info ${!pathStatus ? 'animated fadeInUp delay-4s' : ''}`}>
          <h2 className="home-contact-info-heading">Where to find me</h2>
          <div className="home-contact-icons">
            <a href="https://www.linkedin.com/in/bheki-maenetja-350a95192/" target="_blank">
              <i className="fab fa-linkedin-in fa-4x"></i>
            </a>
            <a href="mailto:bhekimaenetja@gmail.com" target="_blank">
              <i className="fas fa-envelope fa-4x"></i>
            </a>
            <a href="https://github.com/" target="_blank">
              <i className="fab fa-github-alt fa-4x"></i>
            </a>
            <a href="https://twitter.com/bheki_maenetja" target="_blank">
              <i className="fab fa-twitter fa-4x"></i>
            </a>
          </div>
        </footer>
      </section>
      </>
    )
  }
}

export default Home