import React from 'react'
import { Link } from 'react-router-dom'

import Intro from './Intro'
import Coding from './Coding'
import Interests from './Interests'

import '../../stylesheets/aboutPage.scss'

class About extends React.Component {

  state = {
    sectionName: 'intro',
    info: {
      intro: 'Hello there! My name is Bheki. I am a twenty-something year old, proudly South African junior full-stack web developer and a recent General Assembly graduate. But more than that, I’m a problem-solver, builder and general creative. Through computer programming and, more specifically, web development I have been able to build weird and wonderfully machinations from games, quirky superhero websites and much more. Though I have no official workplace experience, my years as a hobbyist developer as well as my time as a full-time software engineering student at GA have given me a wealth of experience in both the theoretical and practical elements of software development. Coupled with my soon-to-be-completed computer science degree I will have a level of skill and expertise that will put me in good stead for my first venture into the industry.',
      coding: 'My interest in computers stems from a fundamental interest in solving problems. Be it mathematical or logical, I\'ve got a knack for neatly solving puzzling problems. Towards to the end of my high school years I dove deep into programming, building everything from ciphers to wacky versions of Deal or No Deal. My perusal of numerous publications enhanced my understanding of technology\'s role in society. My interest grew and my views matured. Teamed with a healthy helping of sci-fi films and novella, it became clear to me that I have to explore the world of computers. In the long-term I hope to explore how computers can be applied to abstract ideas. Today, a computer can help you find the nearest restaurant or check the weather but, imagine if it could philosophise about the meaning of life. Think what that would add to our understanding of not just computers but ourselves. Such ideas would involve fields both within and outside computer science.',
      interests: 'Outside of the technosphere I have a multitude of interests. I’m an avid moviegoer with a particular love for ‘thinking man’s’ sci-fi films such as Blade Runner, Arrival, Interstellar, Inception, 2001 and the like. This is also reflected - to a lesser extent - in the books I read. In terms of literature, my primary interest is comics, specifically Marvel’s Avengers, though I don’t mind the occasional DC one-shot. I dibble and dabble in fan-fiction but I don’t think you’ll see my work anytime soon. I’m also an investor. I’m no Warren Buffet but I have done well for myself with holdings in the big tech stocks and some lesser known ones.'
    }
  }

  changeSections = (e) => {
    this.setState({ sectionName: e.target.name })
  }
  
  render() {
    const { sectionName, info } = this.state
    const sections = {
      intro: <Intro info={info[sectionName]} />,
      coding: <Coding info={info[sectionName]} />,
      interests: <Interests info={info[sectionName]} />
    }
    return (
      <>
      <section className="about-main-container animated faster zoomIn">
        <header className="about-header">
          <Link className="about-back-button" to={{ pathname: "/", state: { prevPath: this.props.location.pathname } }}>
            <i className="fas fa-home fa-5x"></i>
          </Link>
          <h1 className="about-main-heading">About Me</h1>
        </header>
        <div className="about-notebook">
          <aside className="about-aside">
            <button className={`about-aside-button animated ${sectionName === 'intro' ? 'button-active' : ''}`} name="intro" onClick={this.changeSections}>Introduction</button>
            <button className={`about-aside-button ${sectionName === 'coding' ? 'button-active' : ''}`} name="coding" onClick={this.changeSections}>Computers</button>
            <button className={`about-aside-button ${sectionName === 'interests' ? 'button-active' : ''}`} name="interests" onClick={this.changeSections}>Interests</button>
          </aside>
          <main className="about-section-container">
            {sections[sectionName]}
          </main>
        </div>
      </section>
      </>
    )
  }
}

export default About