import React from 'react'
import { Link } from 'react-router-dom'

import "react-responsive-carousel/lib/styles/carousel.min.css"
import { Carousel } from 'react-responsive-carousel'

import '../../stylesheets/expPage.scss'

class Experience extends React.Component {

  state = {
    experiences: [
      {
        name: 'Oxford Royale Academy',
        dates: '7 Jul \'19 - 3 Aug \'19',
        description: ' In July of 2019 I partook in the iOS and Web Development summer course. I was tasked with building an iOS image filter application. The app had to allow users to select one image from a selection of images and apply an image filter to that image. Throughout this course I learnt the foundational principles of software development. Additionally, I gained practical experience in both web and mobile app development.',
        image: 'https://i.ytimg.com/vi/Yq8p9CT_GU4/maxresdefault.jpg'
      },
      {
        name: 'General Assembly',
        dates: '2 Dec \'19 - 6 Mar \'20',
        description: 'For four months I was a full-time student on the Software Engineering Immersive course. I learnt a multitude of programming languages and web technologies. I attained invaluable, practical experience in both frontend and backend web development. I was also introduced to many of the tools of the trade within the software development industry.',
        image: 'https://ga-shop.s3.amazonaws.com/production/store/locationmasthead/3/image/retina-4fac23dea54e8327c47a90dcf5d1e70c.jpg'
      },
      {
        name: 'Skills',
        dates: 'N/a',
        description: 'Look at all those skills',
        image: 'https://images.unsplash.com/photo-1515879218367-8466d910aaa4?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&w=1000&q=80'
      }
    ],
    currentExp: null,
    pageIndex: 0
  }

  componentDidMount() {
    const { experiences } = this.state
    this.setState({ currentExp: experiences[0] })
  }

  changePages = pageIndex => {
    const { experiences } = this.state
    this.setState({ pageIndex, currentExp: experiences[pageIndex] })
  }

  render() {
    const { experiences, currentExp, pageIndex } = this.state
    if (!currentExp) return null
    return (
      <>
      <section className="exp-main-container animated faster zoomIn">
        <header className="exp-header">
          <Link className="exp-back-button" to={{ pathname: "/", state: { prevPath: this.props.location.pathname } }}>
            <i className="fas fa-home fa-4x"></i>
          </Link>
          <h1 className="exp-main-heading">Experience</h1>
        </header>
        <Carousel
          showThumbs={false} 
          showStatus={false} 
          infiniteLoop={true} 
          autoPlay={false} 
          stopOnHover={false}
          swipeable={false}
          dynamicHeight={true}
          width={"100vw"}
          selectedItem={pageIndex}
          onChange={this.changePages}
        >
          {experiences.map(exp => (
            <>
            <div className="exp-carousel-page" style={{ backgroundImage: `url(${exp.image})` }}>
              <div className="page-legend" style={{ backgroundColor: 'rgba(0,0,0, 0.5)', opacity: '1' }}>
                <p>{exp.name}</p>
              </div>
            </div>
            </>
          ))}
        </Carousel>
        {pageIndex === experiences.length - 1 ? 
        <>
        <div className="exp-info">
          <div className="skill-container">
            <h1>{currentExp.name}</h1>
            <br />
            <div className="skill-icons">
              <div className="skill-icon-container">
                <i className="fab fa-python fa-5x"></i>
                <p className="skill-icon-name">Python 3</p>
              </div>
              <div className="skill-icon-container">
                <i className="fab fa-react fa-5x"></i>
                <p className="skill-icon-name">ReactJS</p>
              </div>
              <div className="skill-icon-container">
                <i className="fab fa-js-square fa-5x"></i>
                <p className="skill-icon-name">JavaScript (ES6)</p>
              </div>
              <div className="skill-icon-container">
                <i className="fab fa-node-js fa-5x"></i>
                <p className="skill-icon-name">NodeJS</p>
              </div>
              <div className="skill-icon-container">
                <i className="fab fa-html5 fa-5x"></i>
                <p className="skill-icon-name">HTML5</p>
              </div>
              <div className="skill-icon-container">
                <i className="fab fa-css3-alt fa-5x"></i>
                <p className="skill-icon-name">CSS3</p>
              </div>
              <div className="skill-icon-container">
                <i className="fab fa-sass fa-5x"></i>
                <p className="skill-icon-name">SASS</p>
              </div>
              <div className="skill-icon-container">
                <i className="fab fa-swift fa-5x"></i>
                <p className="skill-icon-name">Swift</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-postgresql-plain fa-5x"></i>
                <p className="skill-icon-name">PostgreSQL</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-django-plain fa-5x"></i>
                <p className="skill-icon-name">Django</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-mongodb-plain fa-5x"></i>
                <p className="skill-icon-name">MongoDB</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-express-original fa-5x"></i>
                <p className="skill-icon-name">Express.js</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-visualstudio-plain fa-5x"></i>
                <p className="skill-icon-name">VS Code</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-c-plain fa-5x"></i>
                <p className="skill-icon-name">C</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-git-plain fa-5x"></i>
                <p className="skill-icon-name">Git</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-github-plain fa-5x"></i>
                <p className="skill-icon-name">Github</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-heroku-original fa-5x"></i>
                <p className="skill-icon-name">Heroku</p>
              </div>
              <div className="skill-icon-container">
                <i className="devicon-webpack-plain fa-5x"></i>
                <p className="skill-icon-name">Webpack</p>
              </div>
            </div>
          </div>
        </div>
        </>
        :
        <>
        <div className="exp-info">
          <div className="text-container">
            <h1>{currentExp.name}</h1>
            <h2>{currentExp.dates}</h2>
            <br />
            <p>{currentExp.description}</p>
          </div>
        </div>
        </> 
        }
      </section>
      </>
    )
  }
}

export default Experience